import React from 'react'

const Button = ({ color, text, onClick }) => {
    // const onClick = () => {
    //     console.log('Button Pressed')
    // }
    
    return (
    <button 
        onClick={onClick} 
        className='btn' 
        style={{backgroundColor: color}}
    >
        {text}</button>
    )
}

Button.defaultProps = {
    color: 'steelblue',
    text: 'Add',
}



export default Button
